html, body {
  margin: 0px;
  padding: 0px;
}

p, h1, h2, h3, h4, h5, span {
  margin: 0px;
  padding: 0px;
}

a {
  text-decoration: none;
}

input {
  outline: none;
}

.main-grid-container {
  width: 100%;
  height: 95vh;
  display: grid;
  grid-template-columns: 340px 1fr;
  grid-template-rows: auto auto 1fr;
  grid-template-areas: "header header" "leftMenu routing" "leftMenu body";
}

.header {
  grid-area: header;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 22px 50px 15px 50px;
  border-bottom: 2px solid black;
}
.header .right-menu {
  display: flex;
  flex-direction: row;
  gap: 45px;
  justify-content: center;
  align-items: center;
}
.header .right-menu .change-theme-div {
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  gap: 5px;
  justify-content: center;
  align-items: center;
  padding: 2px 5px 5px 5px;
  cursor: pointer;
}
.header .right-menu .change-theme-div img {
  width: 30px;
  height: 30px;
}
.header .right-menu .change-theme-div p {
  text-align: center;
  font-size: 14px;
}
.header .right-menu .profile-menu-wrapper {
  position: relative;
}
.header .right-menu .profile-menu-wrapper .setting-list {
  padding: 10px 0px 10px 0px;
  width: 100%;
  bottom: -45px;
  left: 0px;
  position: absolute;
  background-color: #e6e4e4;
  color: red;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  cursor: pointer;
}
.header .right-menu .profile-menu-wrapper .setting-list p {
  font-weight: bold;
}
.header .right-menu .profile-menu-wrapper .setting-list p:hover {
  opacity: 0.5;
}
.header .right-menu .profile-menu-wrapper .profile-menu {
  background-color: #F60621;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 7px 10px 7px 10px;
  border-radius: 10px;
  cursor: pointer;
}
.header .right-menu .profile-menu-wrapper .profile-menu p {
  color: white;
}

.auth-div-main {
  height: 97vh;
  width: 97vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 50px;
}
.auth-div-main img {
  width: 250px;
  height: 80px;
}
.auth-div-main .auth-block {
  max-width: 500px;
  height: 400px;
  background-color: #EEEEEE;
  border-radius: 25px 0px 25px 0px;
  border-right: 4px solid black;
  border-bottom: 4px solid black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  padding-bottom: 25px;
}
.auth-div-main .auth-block .label-wrapper {
  width: 100%;
  display: flex;
  margin-top: 20px;
  justify-content: center;
  align-items: center;
  gap: 24px;
}
.auth-div-main .auth-block .label-wrapper .label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}
.auth-div-main .auth-block .label-wrapper .label p {
  font-size: 22px;
  font-family: Arial;
  font-weight: 400;
}
.auth-div-main .auth-block .label-wrapper .bands {
  width: 50px;
  display: flex;
  flex-direction: column;
  gap: 9px;
  flex-grow: 2;
}
.auth-div-main .auth-block .label-wrapper .bands .band-red {
  width: 100%;
  height: 4px;
  background-color: red;
}
.auth-div-main .auth-block .label-wrapper .bands .band-black {
  width: 100%;
  height: 4px;
  background-color: black;
}
.auth-div-main .auth-block .block-login {
  width: 100%;
  padding: 5px 30px 0px 30px;
  box-sizing: border-box;
}

.error-block {
  background-color: #F25869;
  border-radius: 5px;
  padding: 10px 15px 10px 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.error-block p {
  color: white;
}

.wrapper-auth-input-field {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
}
.wrapper-auth-input-field img {
  width: 22px;
  height: 26px;
  position: absolute;
  top: 20px;
  left: 10px;
}
.wrapper-auth-input-field .auth-input-field {
  width: 100%;
  height: 60px;
  outline: none;
  border-radius: 5px;
  padding-left: 42px;
  color: #999999;
  font-size: 18px;
  border: none;
}
.wrapper-auth-input-field .empty-input-field {
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2);
  border: 1px solid #DF1229;
}

.auth-input-group {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 25px;
}

.auth-btn-login {
  width: 50%;
  padding: 16px 15px 16px 15px;
  background-color: #F60621;
  box-shadow: 0px 0px 15px gray;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.2s;
}
.auth-btn-login:hover {
  transform: scale(1.1);
  box-shadow: 0px 0px 25px gray;
}
.auth-btn-login p {
  color: white;
  font-size: 22px;
  font-family: sans-serif;
  text-align: center;
}

.wrapper-left-bar-div {
  grid-area: leftMenu;
  width: 100%;
  height: 100%;
  margin-top: 35px;
  display: flex;
  justify-content: center;
  border-right: 1px solid rgba(0, 0, 0, 0.3);
}

.left-bar-div {
  display: flex;
  width: 280px;
  justify-content: center;
  height: max-content;
  border-radius: 10px;
  border: 1px solid red;
  padding: 5px 10px 30px 10px;
  flex-direction: column;
  gap: 30px;
}
.left-bar-div .menu-item {
  display: flex;
  flex-direction: column;
  gap: 7px;
}
.left-bar-div .menu-item .label {
  font-size: 16px;
}
.left-bar-div .menu-item .diliver {
  width: 100%;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.1);
}
.left-bar-div .menu-item .item {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 7px;
  padding: 4px 0px 4px 13px;
  border-left: 2px solid gray;
  cursor: pointer;
  text-transform: none;
}
.left-bar-div .menu-item .item .name {
  font-size: 18px;
}
.left-bar-div .menu-item .item .count-div {
  padding: 2px 5px;
  background-color: black;
  color: yellow;
  border-radius: 4px;
}

.item-div-active {
  background-color: red;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.item-mobile-div-active {
  background-color: red;
  border-radius: 10px;
}

.item-p-active {
  color: white;
}

.item-p-unactive {
  color: red;
}

.current-path-div {
  max-width: 1400px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 25px;
  grid-area: routing;
  background-color: #EEEEEE;
  border-radius: 10px;
  color: red;
  margin-top: 35px;
  margin-right: 50px;
  margin-left: 20px;
}

.mobile-menu-groups {
  display: none;
  flex-direction: row;
  gap: 20px;
  grid-area: mobileMenu;
  justify-content: center;
  margin-top: 15px;
  flex-wrap: wrap;
  border-bottom: 1px solid #b3b3b3;
  padding: 0px 20px 15px 20px;
}
.mobile-menu-groups .group {
  display: flex;
  flex-direction: row;
  gap: 15px;
  justify-content: center;
  align-items: center;
}
.mobile-menu-groups .group .label {
  font-size: 18px;
}
.mobile-menu-groups .group .item {
  padding: 5px 15px 5px 15px;
  display: flex;
  flex-direction: row;
  gap: 12px;
  justify-content: center;
  align-items: center;
  margin-right: 15px;
}
.mobile-menu-groups .group .item p {
  font-size: 20px;
}
.mobile-menu-groups .group .item .count {
  padding: 3px 5px 3px 5px;
  background-color: black;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: yellow;
}

.body {
  max-width: 1400px;
  display: flex;
  grid-area: body;
  margin-left: 20px;
  margin-top: 20px;
  margin-right: 50px;
}

.edited-block {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  gap: 15px;
  justify-content: flex-start;
  padding-left: 15px;
  align-items: center;
}

.edited-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 15px 5px 15px;
  font-size: 18px;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  color: white;
}

.edited-btn-text {
  color: white;
}

.edited-btn-green {
  background-color: green;
}

.edited-btn-red {
  background-color: red;
}

.edited-btn-yellow {
  background-color: #cec131;
  box-shadow: 0px 0px 15px #aeaeae;
}

.description-element {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 80px;
  background-color: #EEEEEE;
  font-size: 20px;
  font-weight: bold;
  border-bottom: 1px solid #5F5F5F;
  border-top: 1px solid #5F5F5F;
  padding-left: 10px;
  padding-right: 10px;
}

.descriptio-left {
  border: 1px solid #5F5F5F;
}

.description-right {
  border: 1px solid #5F5F5F;
}

.left-element {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.right-element {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.item-table-value {
  border-bottom: 1px solid #E9ECEF;
  box-sizing: border-box;
  height: 100%;
  padding: 15px 15px 15px 10px;
  border-right: 1px solid #E9ECEF;
}

.input-edit-value {
  width: 100%;
  height: 50px;
  border-radius: 4px;
  padding-left: 10px;
  box-sizing: border-box;
}

.add-order-div {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding-right: 20px;
}

.order-div {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.count-orders {
  width: max-content;
  background-color: #EEEEEE;
  border-radius: 10px;
  padding: 6px;
  color: red;
  font-weight: bold;
}

.btn-add-new-order {
  width: max-content;
  padding: 8px 15px 8px 15px;
  background-color: #E00019;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: white;
  border-radius: 10px;
  cursor: pointer;
  margin-bottom: 25px;
}

.btn-order-group {
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.btn-order-unactive {
  opacity: 0.5;
}

.input-invalid {
  border: 1px solid red;
  box-shadow: 0px 0px 15px red;
}

.btn-order {
  width: max-content;
  padding: 8px 15px 8px 15px;
  background-color: #E00019;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: white;
  border-radius: 10px;
  cursor: pointer;
}

.btn-approve-new-order {
  background-color: green;
}

.btn-cancel-new-order {
  background-color: #E00019;
}

.input-new-order-wrapper {
  display: flex;
  flex-direction: column;
  gap: 7px;
}
.input-new-order-wrapper .label {
  font-size: 20px;
  font-weight: bold;
  color: #E00019;
}
.input-new-order-wrapper .input-new-order {
  max-width: 400px;
  border-radius: 10px;
  border: 1px solid black;
  padding: 10px 15px 10px 15px;
}

.mobile-table-group {
  display: none;
  flex-direction: column;
  gap: 30px;
  padding-right: 20px;
}

.mobile-table {
  display: grid;
  grid-template-columns: 100px 1fr;
  grid-auto-rows: 100px;
  justify-content: center;
  align-items: center;
}

.element-mobile-table {
  border: 1px solid gray;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.desc-element-mobile-table {
  background-color: #EEEEEE;
}

.btn-edit-group {
  grid-column-start: 1;
  grid-column-end: 3;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  grid-column-start: 0;
  grid-column-end: 3;
  width: 100%;
  height: 80px;
  background-color: rgba(0, 0, 0, 0);
}

.mobile-table-input {
  width: 100%;
  height: 50px;
  border-radius: 5px;
  border: 1px solid gray;
  margin: 0px 25px 0px 25px;
  padding: 0px 0px 0px 16px;
}

@media screen and (max-width: 1200px) {
  .table-container {
    display: none;
  }
  .mobile-table-group {
    display: flex;
  }
  .left-bar-div .menu-item .item .name {
    display: none;
  }
  .left-bar-div .menu-item .item {
    border-left: none;
  }
  .left-bar-div {
    max-width: 80px;
    position: fixed;
  }
  .main-grid-container {
    grid-template-columns: 130px 1fr;
  }
  .left-bar-div .menu-item .label {
    display: none;
  }
}
@media screen and (max-width: 850px) {
  .body {
    margin-right: 10px;
  }
  .wrapper-left-bar-div {
    display: none;
  }
  .mobile-menu-groups {
    display: flex;
  }
  .main-grid-container {
    width: 100%;
    height: 95vh;
    display: grid;
    grid-template-columns: 340px 1fr;
    grid-template-rows: auto auto auto 1fr;
    grid-template-areas: "header header" "mobileMenu mobileMenu" "routing routing" "body body";
  }
  .current-path-div {
    margin-right: 10px;
    margin-left: 10px;
  }
  .header {
    grid-area: header;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 22px 50px 15px 50px;
    border-bottom: 2px solid black;
    gap: 15px;
  }
  .header .right-menu .change-theme-div p {
    display: none;
  }
  .header .right-menu .profile-menu-wrapper .profile-menu p {
    display: none;
  }
}
@media screen and (max-width: 420px) {
  .auth-div-main .auth-block .label-wrapper .label p {
    font-size: 16px;
  }
  .error-block p {
    font-size: 15px;
  }
  .wrapper-auth-input-field .auth-input-field {
    font-size: 16px;
  }
  .auth-btn-login p {
    font-size: 16px;
  }
}

